___CSS_LOADER_EXPORT___.push([module.id, `body {
  padding: 0;
  margin: 0;
}

#logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  display: grid;
  grid-template-rows: minmax(150px, auto) 1fr;
  grid-template-columns: 250px 1fr;
  height: 100vh;
}

#task-list {
  grid-column: 2/3;
  grid-row: 1/-1;
  display: flex;
  flex-direction: column;
  position: relative;
}

.project-container {
  display: flex;
  justify-items: center;
  align-items: center;
  margin-bottom: 10px;
}

.default-project {
  font-weight: 900;
  text-align: center;
  border: 1px solid black;

  font-size: 0.8rem;
  width: 15px;
  height: 15px;

  margin-right: 5px;
}

.project {
  background: #ddd;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
  flex: 1;
  display: flex;
}

.project:hover {
  background: #bbb;
}

.project-butt-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.project-create > form {
  display: flex;
  flex: 1;
  gap: 10px;
}
.project-create > form > input {
  margin: 5px 10px;
  width: 80%;
}

.project-butt-create,
.project-butt-rename,
.project-butt-delete,
.update-button,
.delete-button {
  vertical-align: middle;
  width: 80px;
  height: 30px;
  background-color: white;
  border-width: 1px;
  color: lightblue;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  padding: 2px 0px;
  border-radius: 5px;
  transition:
    background 0.2s,
    color 0.2s;
}

.project-butt-create:hover,
.update-button:hover {
  /* width: 200px; */
  color: white;
  background-color: lightblue;
}

/* .project-container button, */
.project-butt-delete,
.delete-button {
  width: 50px;
  color: #d9534f;
  padding: 5px 10px;
}
.project-butt-rename {
  /* width: 50px; */
  color: #00cc00;
  /* padding: 5px 10px; */
  font-weight: 500;
  font-size: 0.9rem;
}

.project-butt-rename,
.project-butt-delete {
  align-self: flex-end;
  align-self: flex-start;
}

/* .project-container button:hover, */
.project-butt-delete:hover,
.delete-button:hover {
  background: #cc0000;
  color: white;
}

.project-butt-rename:hover {
  background: #00cc00;
  color: white;
}

.update-button,
.delete-button {
  width: 150px;
  font-size: 0.9rem;
  font-weight: 500;
}
.checkmark {
  display: none;
  font-size: 1.2rem;
  text-align: center;
  color: black;
}

.task > span {
  flex: 1;
}

.task-container {
  margin: 15px;
}

.task-container > * {
  padding: 20px;
}

.task-info {
  margin-left: 15px;
  margin-right: 8px;
  border: 2px solid darkgray;
  border-top: none;
  border-radius: 0 0 10px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.task-info-field {
  display: flex;
  gap: 15px;
  width: 100%;
}
.task-info-field > * {
  border: 1px solid black;
  border-radius: 5px;
  padding: 3px;
  width: 100%;
}
.task-info-field > label {
  border: none;
  width: 100px;
  text-align: right;
}

.task-create {
  border-radius: 10px;
  align-items: center;
}

.task.soon-due {
  /* background-color: yellow; */
  /* background-color: rgba(255, 0, 0, 0.5); */
  background-color: #faa;
}
.task.already-due {
  /* background-color: rgba(0, 0, 0, 0.5); */
  color: white;
  background-color: #777;
  background-color: #444;
}

.task {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 12px 16px;
  border-radius: 10px;
  background-color: lightgray;
  transition:
    background-color 200ms ease,
    transform 100ms ease;
  cursor: pointer;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}

.task:hover {
  background-color: rgba(128, 128, 128, 0.8);
  transform: translateY(2px);
}

.task-creation {
  background: #f7f7f7;
  border: 2px dashed #ccc;
  border-radius: 12px;
  margin: 15px;
  padding: 12px;
  text-align: center;
  color: #888;
  font-weight: 600;
  cursor: pointer;
  transition:
    background 0.2s ease,
    color 0.2s ease;
}

.task-creation:hover {
  background: #e8e8e8;
  color: #555;
}

.task-priority {
  border: 1px solid #555;
  border-radius: 50%;
  height: 20px;
  width: 20px;
}
.task-priority.high-priority {
  /* background-color: rgba(255, 0, 0, 0.5); */
  background-color: rgb(253, 126, 126);
}
.task-priority.medium-priority {
  /* background-color: rgba(255, 255, 0, 0.8); */
  background-color: rgb(248, 246, 0);
}
.task-priority.low-priority {
  /* background-color: rgba(100, 100, 255, 0.4); */
  background-color: rgb(169, 165, 255);
}

.popup-container {
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.popup {
  display: flex;
  background-color: #fff;
  width: 45%;
  margin: 10% auto;
  padding: 50px 20px;
  border-radius: 5px;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.2);
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  align-items: center;
}

.popup-info > span {
  position: absolute;
  top: 10px;
  left: 45%;
  font-weight: bold;
}

.popup-info > button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

.form-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-field {
  width: 80%;
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.form-field > label {
  width: 100px;
  text-align: right;
}

.error-text {
  background-color: #fdd;
  border: 1px solid #a44;
  color: #b44;
  padding: 15px;
  margin: 10px;
  display: none;
}

.task-completed {
  color: gray;
  background-color: #eee;
}
.task-completed > span {
  text-decoration: line-through;
}
